<template>
  <div>

    <!-- Form Submit/Reset Buttons -->
    <b-card bg-variant="light" class="mb-3">

      <b-alert v-if="message" show :variant="error ? 'danger' : 'success'">{{message}}</b-alert>

      <b-spinner v-if="loading" label="Loading..."></b-spinner>

      <b-form-group
        :id="`input-group-submit-${formid}`"
        :label-for="`input-submit-${formid}`"
        class="mb-1"
        :label="$props.label"
        label-size="lg"
        label-class="font-weight-bold pt-0"
      >
        <b-button
          :disabled="!!$props.disabled"
          type="button"
          variant="primary"
          class="m-2"
          @click="save()"
        >Save Draft</b-button>
        <b-button
          :disabled="!!$props.disabled || !$props.valid"
          type="button"
          :variant="!$props.valid ? 'secondary' : 'success'"
          class="m-2"
          @click="submit()"
        >Submit {{!$props.valid ? '(Not Completed)' : '(Completed)'}}</b-button>
      </b-form-group>
    </b-card>

  </div>
</template>

<script>

import api from '@/services/api-services'
import {genID} from '@/services/data-services'

export default {
  name: "submit-input",
  props: {
    data: Object,
    label: String,
    disabled: Boolean,
    valid: Boolean,
    init: Object
  },
  data() {
    return {
      formid: genID(),
      id: null,
      error: false,
      message: '',
      loading: false
    }
  },
  methods: {
    async create () {
      try {

        this.loading = true

        // if ID provided then update data, otherwise create new document
        const response = await api.post(`api/nominations/create`, this.data)

        const { data={} } = response || {}
        const { id=null, category='' } = data || {}
        this.loading = false
        this.message = 'New nomination created.'
        this.error = false

        if (id && category)
          return await this.$router.push(`/nominations/${category}/${id}`)
        else {
          this.message = 'Server Error: Could not create nomination.'
          this.error = true
        }

      } catch (err) {
        console.error(err);
        this.error = true
        this.loading = false
        this.message = 'Nomination could not be created.'
      }
    },
    async save () {
      try {

        this.loading = true

        // if ID provided then update data, otherwise create new document
        console.log('Saving:', this.data)
        await api.post(`api/nominations/update/${this.data.id}`, this.data)
        this.loading = false
        this.message = 'Nomination saved.'
        this.error = false
      } catch (err) {
        console.error(err);
        this.error = true
        this.loading = false
        this.message = 'Nomination was not saved.'
      }
    },
    async submit () {
      try {
        this.loading = true
        const response = await api.post(`api/nominations/submit/${this.data.id}`, this.data)
        console.log(response)
        this.error = false
        this.loading = false
        this.message = 'Successfully submitted nomination!'
      } catch (err) {
        console.error(err);
        this.error = true
        this.loading = false
        this.message = 'Nomination could not be submitted.'
      }
    }
  },
  mounted() {
    // check if ID parameter exists
    if (!this.$route.params.id) {
      this.create()
    }
  }
};
</script>
