<template>
  <div>

    <pageheader header="My Nominations" lead="View Your Saved Nominations" />

    <b-alert v-if="message" show :variant="error ? 'danger' : 'success'">{{message}}</b-alert>

    <!-- deletion modal -->
    <b-modal id="modal-delete" title="Delete Nomination" @ok="remove">
      <p class="my-4">Delete Nomination {{ selected ? selected._id : null}}?</p>
    </b-modal>

    <b-container>
      <b-row v-if="!loaded" class="vh-50 text-center" align-v="center">
        <b-col id="spinner"><b-spinner label="Loading..."></b-spinner></b-col>
      </b-row>
      <b-row v-if="loaded && nominations.length === 0" class="vh-70 text-center p-5" align-v="center">
        <b-col>
          <b-alert show variant="warning">You have no saved or submitted nominations.</b-alert>
        </b-col>
      </b-row>
      <b-row class="border-bottom-2" v-for="(nomination, counter) in nominations" v-bind:key="counter">
        <b-col>
          <b-row class="m-2" align-v="center">
            <b-col cols="1">
              {{nomination.submitted ? 'Submitted' : 'Draft'}}
            </b-col>
            <b-col>{{nomination.category.replace('-', ' ').toUpperCase()}}</b-col>
            <b-col v-if="!!nomination.title">{{nomination.title}}</b-col>
            <b-col v-if="!nomination.title">
              <b>Nominee:</b> {{nomination.nominees[0].firstname}} {{nomination.nominees[0].lastname}}
            </b-col>
            <b-col cols="5">
              <b-button class="m-2" v-b-toggle="`nomination-details-${counter}`" role="tab">
                <span class="when-open"><BIconChevronCompactDown /></span>
                <span class="when-closed"><BIconChevronCompactUp /></span> Details
              </b-button>
              <b-button
                :disabled="nomination.submitted"
                class="m-2"
                @click="reroute(`/nominations/${nomination.category}/${nomination._id}`)">Edit</b-button>
              <b-button
                :disabled="nomination.submitted"
                class="m-2"
                @click="selected=nomination"
                v-b-modal.modal-delete>Delete</b-button>
            </b-col>
          </b-row>
          <b-row class="bg-light">
            <b-col>
              <b-collapse :id="`nomination-details-${counter}`" role="tabpanel">
                <b-container class="p-3">
                  <b-row class="mb-4">
                    <b-col><h3 class="text-center">Nomination Details</h3></b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="2">
                      <h4>Organization</h4>
                    </b-col>
                    <b-col>
                      <b-table stacked :items="[{name: lookup('organizations', nomination.organization)}]"></b-table>
                    </b-col>
                  </b-row>
                  <b-row  v-if="!!nomination.title">
                    <b-col>
                      <h4>Nomination Title</h4>
                    </b-col>
                    <b-col>{{nomination.title}}</b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="2"><h4>Nominees</h4></b-col>
                    <b-col>
                      <b-table stacked :items="nomination.nominees"></b-table>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="2"><h4>Nominators</h4></b-col>
                    <b-col>
                      <b-table stacked :items="nomination.nominators"></b-table>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="2"><h4>Contacts</h4></b-col>
                    <b-col>
                      <h5>Primary</h5>
                      <b-table stacked :items="[nomination.contacts.primary]"></b-table>
                      <h4>Video</h4>
                      <b-table stacked :items="[nomination.contacts.video]"></b-table>
                      <h4>Acknowledgment</h4>
                      <p>{{ nomination.acknowledgment.toUpperCase() }}</p>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="2"><h4>Evaluation</h4></b-col>
                    <b-col>
                      <b-table stacked :items="[nomination.evaluation]"></b-table>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="2"><h4>Attachments</h4></b-col>
                    <b-col>
                      <b-table stacked :items="nomination.attachments"></b-table>
                    </b-col>
                  </b-row>
                </b-container>
              </b-collapse>
            </b-col>
          </b-row>
        </b-col>

      </b-row>
    </b-container>
  </div>
</template>

<script>


import api from '@/services/api-services'
import formServices from '../../services/form-services'
import pageheader from '@/components/common/PageHeader'
import { BIconChevronCompactUp, BIconChevronCompactDown } from 'bootstrap-vue'

export default {
  name: 'nomination-list',
  components: {
    pageheader, BIconChevronCompactUp, BIconChevronCompactDown
  },
  data () {
    return {
      loaded: false,
      error: false,
      selected: null,
      message: '',
      nominations: []
    }
  },
  methods: {
    async reroute(uri) {
      await this.$router.push(uri)
    },
    async remove () {
      try {
        this.loading = true
        const { _id = null } = this.selected || {}
        await api.get(`api/nominations/delete/${_id}`, this.data)
        // remove item from local data
        this.selected = null
        this.nominations = this.nominations.filter(nomination => nomination._id !== _id)
        this.error = false
        this.loading = false
        this.message = 'Nomination deleted.'
      } catch (err) {
        console.error(err);
        this.error = true
        this.loading = false
        this.message = 'Nomination could not be deleted.'
      }
    },
    lookup(key, value) {
      return formServices.lookup(key, value)
    }
  },
  mounted () {
    const userid = 'test'
    api.get(`api/nominations/user/${userid}`).then(response => {
      const { data = [] } = response.data || {}
      this.nominations = data
      this.loaded = true
      this.error = false
      this.message = ''
    }).catch (err => {
      console.error(err);
      this.error = true
      this.message = 'Nominations could not be retrieved.'
    })
  }
}
</script>
<style>
.collapsed > .when-open,
.not-collapsed > .when-closed {
  display: none;
}
</style>
